@font-face {
    font-family: 'NarkisimMF';
    src: url('subset-NarkisimMF.eot');
    src: url('subset-NarkisimMF.eot?#iefix') format('embedded-opentype'),
        url('subset-NarkisimMF.woff2') format('woff2'),
        url('subset-NarkisimMF.woff') format('woff'),
        url('subset-NarkisimMF.ttf') format('truetype');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

